<!--  -->
<template>
  <div>
        <Zi bool1="true"  goback="true" data="<" con="新增地址"   >
        <h1 @click="$router.go(-1)" slot="left">
        <
      </h1>
      </Zi>
      <div class="address">
          <ul>
              <li>
                  <input v-model="username" type="text" placeholder="请填写你的姓名">
              </li>
                <router-link tag="li" to="/address/addaddress/addressaddA">
                  <input v-model="val"  type="text" placeholder="小区/写字楼/学校等">
              </router-link>
                <li>
                  <input v-model="address" type="text" placeholder="请填写详细送餐地址">
              </li>
                <li>
                  <input v-model="number" type="text" placeholder="请填写能够联系到您的手机号">
                  <p v-show="numberBool==true">请输入正确手机号</p>
              </li>
                <li>
                  <input v-model="phone" type="text" placeholder="备用联系电话（选填）">
              </li>
          </ul>
      </div>
      <van-button @click="add" type="primary" size="large">新增地址</van-button>
       <div class="tishi" v-show="bool2==true">
        <div class="tishi2">
          <span class="tishi_s1"></span><span class="tishi_s2"></span>
        
        </div>
         <p class="tishi_p"> {{valtishi}}</p>
         <button class="que" @click="que">确认</button>
      </div>
  </div>
</template>

<script>
import Zi from "../../../components/header"
export default {
  data () {
    return {
      valtishi:"",
      bool2:false,
        val:"",
        username:"",
        address:"",
        number:"",
        phone:"",
        geohash:"",
        user_id:"",
        numberBool:false
    };
  },

  components: {Zi},

  computed: {},
activated(){
 if(this.$route.query.name==undefined){
             this.val=""
        }else{
          this.val=this.$route.query.name
          console.log(this.$route.query.name)
        }
},
  mounted(){
        this.user_id=this.$store.state.obj.user_id
       
if(localStorage.getItem("nameList")){
  this.geohash=JSON.parse(localStorage.getItem("nameList")).geohash
}
   
  },

  created(){},

  methods: {
    add(){
      var that=this
      this.http.post("https://elm.cangdu.org/v1/users/"+this.user_id+"/addresses",{
        address:that.address,
          address_detail: that.val,
          geohash: that.geohash,
          name:that.username,
          phone: that.number,
          phone_bk:that.phone,
          poi_type: 0,
          sex: 1,
          tag: "公司",
          tag_type: 4,
      }).then((d)=>{
          if(d.data.status==1){
          var obj={
           address:that.address,
          address_detail: that.val,
          geohash: that.geohash,
          name:that.username,
          phone: that.number,
          phone_bk:that.phone,
          poi_type: 0,
          sex: 1,
          tag: "公司",
          tag_type: 4
          }
          that.$store.commit("addArr",obj)
          that.$router.push("/address/addressChild")
        }else{
          console.log(12324356)
          that.bool2=true;
          that.valtishi=d.data.message
          console.log(d.data.message)
      }
      })
    },
    que(){
      this.bool2=false
    }
  }
}

</script>
<style scoped lang="scss">
.tishi{
  .tishi_p{
  text-align: center;
 margin-top: 0.4rem;
  font-size: 0.3rem;
}
  .que{
    width: 100%;
    display: block;
    background:#4cd964;
  }
  border-radius: 3%;
  position: fixed;
left: 10%;
top: 12%;
  width: 80%;
  height: 40%;
 border:1px solid #ffffff;
 background: white;
 z-index: inherit;
 .tishi2{
       width: 1.5rem;
    height: 1.5rem;
    border: .05rem solid #f8cb86;
    border-radius: 50%;
    margin: 0 auto;
    margin-top: 0.35rem;
    .tishi_s1{
          width: .05rem;
    height: 0.8rem;
    background-color: #f8cb86;
    margin-left: 0.7rem;
    margin-top: 0.2rem;
    display:block
}

.tishi_s2{
      width: .15rem;
    height: .15rem;
    border: 1px;
    display:block;
    border-radius: 50%;
    margin-top: .2rem;
    background-color: #f8cb86;
     margin-left: 0.65rem;
    margin-top: 0.2rem;
}

 }
}
.address{

    padding-top: 0.2rem;
    height: 5rem;
    background: white;
    ul{
    margin-top: 1rem!important;

        li{
                margin-top: 0.1rem; 

            input{
                border-radius: 0.05rem;
                width: 80%;
                display: block;
                height: 80%;
                background:#f2f2f2;
                padding-left: 0.3rem;
                margin: 0 auto;
            }
            
            height: 0.7rem;
        }
    }
}
button{
    width: 90%;
    margin: 0 auto;
    display: block;
    margin-top: 0.7rem;
    height: 0.7rem!important;
    font-size: 0.3rem;
}
</style>


